<template>
<div>
    <v-list>
          <v-list-tile
              value="true"
              v-for="(item, i) in items"
              :key="i"
          >
              <v-list-tile-action>
                  <v-icon v-html="item.icon"></v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                  <v-list-tile-title v-text="item.title"></v-list-tile-title>
              </v-list-tile-content>
          </v-list-tile>
      </v-list>
</div>
</template>

<script>
export default {
    name: 'LeftMenu',

    data () {
        return {
            items: [
                {icon: 'bubble_chart', title: 'Inspire'}
            ]
        }
    }
}
</script>



